import React from "react";
import CartItem from "./CartItem";
import { Box, Button, Grid } from "@material-ui/core";
import { green } from "@material-ui/core/colors";

export default function CartItems(props) {
  const { cart } = props;

  return (
    <Box p={2}>
      {cart.items.length === 0 ? (
        <div className="carrinho-vazio">
          Escolha o seu bloco ou camarote clicando no pre√ßo dos seus respectivos
          dias.
          <ul>
            <li>Confira os produtos escolhidos no carrinho de compras</li>
            <li>Selecione o local de retirada</li>
            <li>Escolha a forma de pagamento e clique em finalizar compra.</li>
          </ul>
        </div>
      ) : (
        <Grid container>
          {cart.items.map(sel => (
            <CartItem key={sel.produto.id} item={sel} />
          ))}
          <Grid item xs={12} style={{ marginTop: 20 }}>
            <Button
              variant="contained"
              color="primary"
              style={{ backgroundColor: green[500], width: "100%" }}
              onClick={props.nextClick}
            >
              ESCOLHER LOCAL DE RETIRADA
            </Button>
          </Grid>
        </Grid>
      )}
    </Box>
  );
}
